import React from "react";
import PortfolioCard from "./portfolioCard";

const Portfolio = () => {
  return (
    <section className="py-16 dark:bg-dark duration-300" id="portfolio">
      <div className="container reveal fade-bottom">
        <div className="w-full text-center">
          <h1 className="font-gothamBlack text-primary text-4xl">Portfolio</h1>
          <p className="font-semibold text-lg mt-3 leading-none dark:text-white">
            Beberapa Project yang Pernah Saya Kerjakan
          </p>
        </div>
        <div className="px-4 mt-10">
          <PortfolioCard />
        </div>
      </div>
    </section>
  );
};

export default Portfolio;
