import React from "react";
import "swiper/css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper";

const SkillsCard = () => {
  const skillList = [
    {
      img: "../img/skills/html.svg",
      name: "HTML",
      description:
        "Saya cukup menguasai HTML, penggunaan tag - tag yang ada pada HTML, dan lain sebagainya",
    },
    {
      img: "../img/skills/css.svg",
      name: "CSS",
      description:
        "Saya cukup menguasai CSS, mulai dari bagaimana cara styling di CSS, mengatur posisi pada CSS, Responsive Design pada CSS, dan lain sebagainya",
    },
    {
      img: "../img/skills/js.svg",
      name: "Javascript",
      description:
        "Saya cukup menguasai Javascript Basic, mulai dari penggunaan variabel, looping, array, Document Object Model (DOM), function, dan lain sebagainya",
    },
    {
      img: "../img/skills/bootstrap.svg",
      name: "Bootstrap",
      description:
        "Saya cukup menguasai Bootstrap, karena memudahkan kita untuk memanggil langsung komponen yang diinginkan",
    },
    {
      img: "../img/skills/tailwind.svg",
      name: "Tailwind CSS",
      description:
        "Saya cukup menguasai Tailwind CSS, mulai dari styling dan lain sebagainya yang hampir sama seperti saat menggunakan CSS dari awal namun lebih cepat dan mudah ketika menggunakan Tailwind CSS",
    },
    {
      img: "../img/skills/react js.svg",
      name: "React JS",
      description:
        "Untuk React JS saya masih tahap belajar, tetapi saya sudah mengetahui bagaimana caranya menggunakan komponen, state dan props, function dan lain sebagainya",
    },
    {
      img: "../img/skills/laravel.svg",
      name: "Laravel PHP Framework",
      description:
        "Saya cukup menguasai Laravel PHP Framework, dari bagaimana caranya melakukan routing, seeding, migration, dan laravel ini mengambil konsel MVC (Model, View, Controller) yang saya cukup mengerti",
    },
    {
      img: "../img/skills/mysql.svg",
      name: "My SQL",
      description:
        "Saya cukup menguasai My SQL, mulai dari mengatur relasi antar tabel, foreign key, dan lain sebagainya",
    },
    {
      img: "../img/skills/web-design.svg",
      name: "Web Mockup",
      description:
        "Saya cukup mengetahui mengenai Web Mockup ketika saya Magang di PT Indovindo Digital Media, mulai dari awal merancang mockup low fidelity sampai hight fidelity",
    },
    {
      img: "../img/skills/pc.svg",
      name: "Software/Hardware Installation & Troubleshooting",
      description:
        "Saya cukup menguasai Software/Hardware Installation & Troubleshooting, karena saya pernah menangani beberapa client yang ingin PC/Laptopnya dilakukan install ulang OS ataupun perbaikan dan mengganti Hardwarenya yang rusak",
    },
    {
      img: "../img/skills/ms office.svg",
      name: "Microsoft Office",
      description:
        "Saya cukup menguasai Microsoft Office, karena aplikasi ini sangat membantu saya dalam berbagai pekerjaan atau tugas tugas yang saya harus kerjakan",
    },
  ];
  return (
    <Swiper
      slidesPerView={1}
      spaceBetween={10}
      loop={true}
      grabCursor={true}
      centeredSlides={true}
      autoplay={{
        delay: 5000,
        disableOnInteraction: false,
      }}
      modules={[Autoplay]}
      breakpoints={{
        640: {
          slidesPerView: 2,
          spaceBetween: 20,
        },
        1024: {
          slidesPerView: 3,
          spaceBetween: 30,
        },
        1280: {
          slidesPerView: 4,
          spaceBetween: 40,
        },
      }}
      className="mySwiper"
    >
      {skillList?.map((skill) => (
        <SwiperSlide key={skill.name}>
          <div className="w-full p-6 sm:w-11/12 bg-white dark:bg-slate-600 dark:border-0 border border-gray-400 rounded-lg shadow hover:bg-gray-100 h-[33.5rem]">
            <div className="flex flex-col items-center">
              <>
                <img
                  className="w-24 h-24 mb-3"
                  src={skill.img}
                  alt={skill.name}
                />
                <h5 className="mb-3 text-xl font-medium text-center dark:text-white">
                  {skill.name}
                </h5>
                <p className="text-center text-secondary dark:text-white">
                  {skill.description}
                </p>
              </>
            </div>
          </div>
        </SwiperSlide>
      ))}
    </Swiper>
  );
};

export default SkillsCard;
