import React from "react";
import profileImg from "../img/adit.png";

function Home(props) {
  return (
    <>
      <section className="py-36 dark:bg-dark duration-300" id="home">
        <div className="container">
          <div className="flex flex-col w-full text-center justify-center items-center">
            <img
              src={profileImg}
              alt="Adit Bagja Septiana"
              className="rounded-full object-cover"
              width="150px"
            />
            <h2 className="mt-5 font-semibold text-primary text-lg ">
              Halo, saya
            </h2>
            <h1 className="font-bold text-2xl dark:text-white">
              Adit Bagja Septiana
            </h1>
            <h2 className="font-semibold text-secondary text-lg dark:text-secondaryDark">
              Fresh Graduate Sistem Informasi
            </h2>
            <button
              // eslint-disable-next-line react/prop-types
              onClick={() => props.goto(props.linkRef.current)}
              className="mt-10 bg-primary w-10 rounded-full px-3 py-2 text-white hover:bg-white hover:outline hover:outline-1 hover:outline-primary hover:text-primary transition duration-500 ease-in-out text-2xl font-bold"
            >
              &darr;
            </button>
          </div>
        </div>
      </section>
    </>
  );
}

export default Home;
