/* eslint-disable react/prop-types */
import { useState, useEffect } from "react";
import React from "react";
import {
  Navbar,
  Typography,
  IconButton,
  Collapse,
} from "@material-tailwind/react";

function Navigation(props) {
  const [openNav, setOpenNav] = useState(false);

  useEffect(() => {
    window.addEventListener(
      "resize",
      () => window.innerWidth >= 960 && setOpenNav(false)
    );
  }, []);

  // fixed navbar
  window.onscroll = function () {
    const header = document.querySelector("nav");
    const fixedNav = header.offsetTop;

    if (window.pageYOffset > fixedNav) {
      header.classList.add("navbar-fixed");
    } else {
      header.classList.remove("navbar-fixed");
    }
  };
  // end of fixed navbar

  // dark mode
  const [theme, setTheme] = useState(
    localStorage.getItem("theme") ? localStorage.getItem("theme") : "system"
  );
  const element = document.documentElement;
  const darkQuery = window.matchMedia("(prefers-color-scheme: dark)");

  const options = [
    {
      icon: "sunny",
      text: "light",
    },
    {
      icon: "moon",
      text: "dark",
    },
    {
      icon: "settings-outline",
      text: "system",
    },
  ];

  function onWindowMatch() {
    if (
      localStorage.theme === "dark" ||
      (!("theme" in localStorage) && darkQuery.matches)
    ) {
      element.classList.add("dark");
    } else {
      element.classList.remove("dark");
    }
  }
  onWindowMatch();

  useEffect(() => {
    switch (theme) {
      case "dark":
        element.classList.add("dark");
        localStorage.setItem("theme", "dark");
        break;
      case "light":
        element.classList.remove("dark");
        localStorage.setItem("theme", "light");
        break;
      default:
        localStorage.removeItem("theme");
        onWindowMatch();
        break;
    }
  }, [theme]);

  darkQuery.addEventListener("change", (e) => {
    if (!("theme" in localStorage)) {
      if (e.matches) {
        element.classList.add("dark");
      } else {
        element.classList.remove("dark");
      }
    }
  });
  // end of dark mode

  const navList = (
    <ul className="mb-4 mt-2 flex flex-col gap-2 lg:mb-0 lg:mt-0 lg:flex-row lg:items-center lg:gap-6">
      <Typography as="li" variant="small" className="p-1 font-normal">
        <button
          onClick={() => props.goto(props.linkRef.current)}
          className="flex items-center hover:text-secondary dark:hover:text-primary"
        >
          About
        </button>
      </Typography>
      <Typography as="li" variant="small" className="p-1 font-normal">
        <a
          href="#pendidikan"
          className="flex items-center hover:text-secondary dark:hover:text-primary"
        >
          Pendidikan
        </a>
      </Typography>
      <Typography as="li" variant="small" className="p-1 font-normal">
        <a
          href="#skill"
          className="flex items-center hover:text-secondary dark:hover:text-primary"
        >
          Skill
        </a>
      </Typography>
      <Typography as="li" variant="small" className="p-1 font-normal ">
        <a
          href="#portfolio"
          className="flex items-center hover:text-secondary dark:hover:text-primary"
        >
          Portfolio
        </a>
      </Typography>
      <Typography as="li" variant="small" className="p-1 font-normal ">
        <a
          href="#follow"
          className="flex items-center hover:text-secondary dark:hover:text-primary"
        >
          Follow
        </a>
      </Typography>
    </ul>
  );
  return (
    <Navbar className="z-10 absolute top-0 left-0 bg-opacity-50 dark:bg-opacity-50 mx-auto w-full py-2 px-4 lg:px-8 lg:py-4 border-0 dark:bg-dark duration-300 shadow-none">
      <div className="container mx-auto flex items-center justify-between text-primary dark:text-white">
        <Typography
          as="a"
          href="/"
          className="mr-4 cursor-pointer py-1.5 font-semibold"
        >
          aditbagja
        </Typography>
        <div className="hidden lg:flex">{navList}</div>
        <Typography
          as="li"
          variant="small"
          className="dark:bg-dark bg-gray-100 rounded duration-100 text-gray-500 dark:text-white"
        >
          {options?.map((opt) => (
            <button
              key={opt.text}
              onClick={() => setTheme(opt.text)}
              className={`w-8 h-8 leading-9 text-xl rounded-full m-1 hover:text-secondary dark:hover:text-primary ${
                theme === opt.text && "text-primary"
              }`}
            >
              <ion-icon name={opt.icon}></ion-icon>
            </button>
          ))}
        </Typography>
        <IconButton
          variant="text"
          className="ml-auto h-6 w-6 text-inherit hover:bg-transparent focus:bg-transparent active:bg-transparent lg:hidden"
          ripple={false}
          onClick={() => setOpenNav(!openNav)}
        >
          {openNav ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              className="h-6 w-6"
              viewBox="0 0 24 24"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="h-6 w-6"
              fill="none"
              stroke="currentColor"
              strokeWidth={2}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M4 6h16M4 12h16M4 18h16"
              />
            </svg>
          )}
        </IconButton>
      </div>
      <Collapse open={openNav}>
        <div className="container mx-auto text-primary dark:text-white">
          {navList}
        </div>
      </Collapse>
    </Navbar>
  );
}

export default Navigation;
