import React, { useEffect, useState } from "react";
import Navigation from "./components/navbar";
import Home from "./components/home";
import Tentang from "./components/tentang";
import Skill from "./components/skill";
import Portfolio from "./components/portfolio";
import PendidikanPengalaman from "./components/pendidikanPengalaman";
import Follow from "./components/follow";
import Footer from "./components/footer";
import { useRef } from "react";

function App() {
  // Scroll Down Button
  const linkRef = useRef(null);

  const goto = (ref) => {
    window.scrollTo({
      top: ref.offsetTop,
      left: 0,
      behavior: "smooth",
    });
  };
  // Back to Top Button
  const [showButton, setShowButton] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.pageYOffset > 300) {
        setShowButton(true);
      } else {
        setShowButton(false);
      }
    });
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  return (
    <>
      <Navigation linkRef={linkRef} goto={goto} />
      <Home linkRef={linkRef} goto={goto} />
      <Tentang linkRef={linkRef} />
      <PendidikanPengalaman />
      <Skill />
      <Portfolio />
      <Follow />
      <Footer />
      {showButton && (
        <button
          onClick={scrollToTop}
          className="animate-pulse fixed bottom-5 right-5 bg-primary w-10 rounded-full px-3 py-2 text-white hover:bg-white hover:outline hover:outline-1 hover:outline-primary hover:text-primary transition duration-500 ease-in-out text-2xl font-bold z-10"
        >
          &uarr;
        </button>
      )}
    </>
  );
}

export default App;
